<template>
    <div class="index" @click.stop>
        <h3 @click="isShow = !isShow ">换内存的方法</h3>
        <div class="content" v-show="isShow">
            <ul>
                <li>{{test}}</li>
                <li>{{test_1}}</li>
                <li>{{test_2}}</li>
                <li>{{test_3}}</li>
                <li>{{test_4}}</li>
                <li>{{test_5}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isShow:false,
            test:'清理好桌面，为了避免其他东西损坏到电脑',
            test_1:'拧后盖螺丝时要注意，可以按照电脑的螺丝排布进行排放，避免螺丝混乱（因为有的电脑螺丝有不一样的）',
            test_2:'翘开后盖时要注意力度，轻一点，以免里面的部件和外壳受损',
            test_3:'注意排线和所有的原器件，以防造成部件无法使用',
            test_4:'断开电池接主板的电源，把金属块先前退，白色的块就可以拿出来啦',
            test_5:'内存卡槽两边有卡扣，注意先往外掰开才能拿出内存',
        }
    },
    mounted(){
        document.body.addEventListener('click',()=>{
            this.isShow = false;
        },false);
    }
}
</script>

<style>
    .index {
        width:700px;
        margin: 10px 35px;
    }

    h3 {
        transition:1s cubic-bezier(.28,.19,.32,.99);
        width: 250px;
    }

    h3:hover {
        cursor:pointer;
        color: red;
        transform: scale(1.03);
    }
</style>
