<template>
    <div class="index" @click.stop>
        <h3 @click="isShow = !isShow ">安装 win10 系统的方法</h3>
        <div class="content" v-show="isShow">
            <ul>
                <h4>安装方法 ：</h4>
                <li>{{test}}</li>
                <li>{{test_1}}</li>
                <li>{{test_2}}</li>
                <li>{{test_3}}</li>
                <li>{{test_4}}</li>
                <li>{{test_5}}</li>
                <li>{{test_6}}</li>
                <li>{{test_7}}</li>
                <li>{{test_8}}</li>
                <li>{{test_9}}</li>
                <li>{{test_10}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isShow:false,
            test:'把制作好Windows系统的U盘插入电脑，按del键进入BIOS页面',
            test_1:'修改boot目录下的CSM...文件，设置成关闭状态（注意按F10保存），修改文件会进入重启，继续按del键进入BIOS页面',
            test_2:'（看需求）修改secret boot...为custom，修改文件会进入重启，继续按del键进入BIOS页面',
            test_3:'在BIOS页面选择U盘启动',
            test_4:'注意要安装在固态硬盘上面（删除和格式化所以的分区和磁盘，可以从内存容量来判断是不是固态硬盘，一般公司是500G的固态硬盘）',
            test_5:'设置用户名为gw。密码设置为空（避免进入系统后又要重新更改密码）',
            test_6:'如果遇到为个人或公司创建时选择为公司创建',
            test_7:'注意把所以已经勾选弹出页面上来的全部取消',
            test_8:'第一次进入系统要“重启一下”',
            test_9:'安装驱动（软件：Suapp Driver Installer）',
            test_10:'进入“计算器管理”，找到“本地用户和组”，选择“用户”，设置密码。'


        }
    },
    mounted(){
        document.body.addEventListener('click',()=>{
            this.isShow = false;
        },false);
    }
}
</script>

<style>
    .index {
        width:700px;
        /* margin: 0 0 0 330px; */
    }

    h3 {
        transition:1s cubic-bezier(.28,.19,.32,.99);
        width: 250px;
    }

    h3:hover {
        cursor:pointer;
        color: red;
        transform: scale(1.03);
    }
</style>
