<template>
    <div class="index" @click.stop>
        <h3 @click="isShow = !isShow ">电脑时间同步</h3>
        <div class="content" v-show="isShow">
            <ul>
                <h4>打开 cmd :</h4>
                <li>{{test}}</li>
                <li>{{test_1}}</li>
                <li>{{test_2}}</li>
                <li>{{test_3}}</li>
                <li>{{test_4}}</li>
                <li>{{test_5}}</li>

                <h4>方法 2 :</h4>
                <li>{{test_6}}</li>
                <li>{{test_7}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isShow:false,
            test:'输入 “ net stop w32time ”',
            test_1:'输入 “ w32tm /unregister ”',
            test_2:'输入 “ w32tm /register ”',
            test_3:'输入 “ net start w32time ”',
            test_4:'输入 “ w32tm /resync /nowait ”',
            test_5:'回到日期与时间进行更新',
            test_6:'进入 BIOS 页面',
            test_7:'对左上角的时间进行强行更改'
        }
    },
    mounted(){
        document.body.addEventListener('click',()=>{
            this.isShow = false;
        },false);
    }
}
</script>

<style>
    .index {
        width:700px;
        margin: 10px 35px;
    }

    h3 {
        transition:1s cubic-bezier(.28,.19,.32,.99);
        width: 250px;
    }

    h3:hover {
        cursor:pointer;
        color: red;
        transform: scale(1.03);
    }
</style>
