<template>
    <div class="index" @click.stop>
        <h3 @click="isShow = !isShow ">拆装台式机的方法</h3>
        <div class="content" v-show="isShow">
            <ul>
                <h4>拆机 :</h4>
                <li>{{test}}</li>
                <li>{{test_1}}</li>
                <li>{{test_2}}</li>
                <li>{{test_3}}</li>
                <li>{{test_4}}</li>
                <li>{{test_5}}</li>
                <li>{{test_6}}</li>
                <li>{{test_7}}</li>
                <h4>装机 ：</h4>
                <li>{{zhuang}}</li>
                <li>{{zhuang_1}}</li>
                <li>{{zhuang_2}}</li>
                <li>{{zhuang_3}}</li>
                <li>{{zhuang_4}}</li>
                <li>{{zhuang_5}}</li>
                <li>{{zhuang_6}}</li>
                <li>{{zhuang_7}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isShow:false,
            test:'首先拔掉主机后面所有的电路，然后拧开旁边的两个大螺丝，打开机箱后盖（有些电脑可能要把其他机盖才能完成工作，这就要看自己手里的机箱结构啦',
            test_1:'拆内存，用微力压开内存条两头的卡锁，往上拔出内存条（装内存条时要注意，，如果是一条内存条装第二卡槽，如何是两条内存条的话装第一、三卡槽）',
            test_2:'拆显卡，拧开固定显卡螺丝（机箱侧面。注意一定要确保所以的螺丝都拧下来了），找到卡锁并打开，往上拔出来',
            test_3:'拆CPU，风扇下面就是CPU，我们拧开固定风扇的几个角，拿掉风扇（水冷）就看到CPU了，如果再安装回去的话要放硅脂，一定要把风扇（水冷）拧紧',
            test_4:'拆光驱，拔掉光驱的两个线头，将其螺丝拧开就可以把光驱拆下来了',
            test_5:'拆电源，拧开固定电源的所有螺丝，把电源线接口全部拔开，和所以的线路一起拿出来',
            test_6:'拆硬盘，拔掉硬盘的两个线头，拧开固定硬盘的螺丝即可',
            test_7:'拆主板，也就是承接上面硬件的这块大板，我们将其螺丝拧开，就可以把主板从机箱里拆出了',
            zhuang:'主板（注意：1、音频、USB的接口要对好机箱的接口2、主板在上固定螺丝时不要太用力）',
            zhuang_1:'硬盘（注意：1、确定安装的位置是不是正确的的2、插入电源）',
            zhuang_2:'电源（注意：1、接上需要的电源线，其他的就不插上电源2、注意电源风扇口的朝向）',
            zhuang_3:'内存（注意：1、打开卡锁）',
            zhuang_4:'CPU（注意：1、放在CPU槽时不能左右移动2、不能用手摸到CPU的芯片面3、注意防呆三角形的标志的朝向，要同一方向）',
            zhuang_5:'水冷/风冷（注意：1、涂抹硅脂要小心，薄薄的一层即可2、风扇的电源接口和水冷头的电源接口，要插到CPU-FAN接口上）',
            zhuang_6:'显卡（注意：1、输出设备接口的位置2、电源线接上）',
            zhuang_7:'整理电脑背部走线，要弄整齐，这样有利于后期的维护工作和机箱内部的散热（就近或者归纳合并2个原则即可）',
        }
    },
    mounted(){
        document.body.addEventListener('click',()=>{
            this.isShow = false;
        },false);
    }
}
</script>

<style>
    .index {
        width:700px;
        margin: 10px 35px;
    }

    h3 {
        transition:1s cubic-bezier(.28,.19,.32,.99);
        width: 250px;
    }

    h3:hover {
        cursor:pointer;
        color: red;
        transform: scale(1.03);
    }
</style>
